$theme: ();

@mixin set-style($group-name, $key, $val) {
  $group: ();
  @if map-has-key($theme, $group-name) {
    $group: map-get($theme, $group-name); }
  $group: map-merge($group, ($key: $val));
  $theme: map-merge($theme, ($group-name: $group)) !global; }

@function get-theme($group-name, $key) {
  $group: map-get($theme, $group-name);
  @return map-get($group, $key); }

@include set-style(light, primary, #000a);
@include set-style(light, secondary, #eee);

@include set-style(dark, primary, #eee);
@include set-style(dark, secondary, #222);

@include set-style(preset, 1, (#e71c, #e13c));
@include set-style(preset, 2, (#1e6c, #1ddc));
@include set-style(preset, 3, (#1dec, #51ec));
@include set-style(preset, 4, (#07ec, #0e6c));
@include set-style(preset, 5, (#68fc, #f68c));
@include set-style(preset, 6, (#8888, #4448));

@mixin gradient($e, $surfix: false) {
  @for $i from 1 through 6 {
    #{$e}.preset#{$i}#{if($surfix, $surfix, '')} {
      background: linear-gradient(get-theme(preset, $i));
      background-attachment: fixed; } } }

@mixin get-style($attr, $i: 1) {
  $from: nth((primary, secondary), $i);
  #{$attr}: get-theme(light, $from);
  @at-root #app.dark & {
    #{$attr}: get-theme(dark, $from); } }
