@import 'theme';

::-webkit-scrollbar {
  width: 8px; }
::-webkit-scrollbar-track {
  background: none; }
::-webkit-scrollbar-thumb {
  background: #8888;
  border-radius: 8px; }
* {
  scrollbar-color: #8888 none;
  scrollbar-width: 8px; }

h1, h3 {
  margin: 0; }

h1 {
  font-weight: normal; }

html, body, #root {
  margin: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  color: #000a;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.avatar {
  position: relative;
  margin-top: 15%;
  width: 50%;
  padding-top: 50%;
  border-radius: 999px;
  overflow: hidden;
  @at-root {
    @include gradient(&); }
  .avatar-text {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    @include get-style(color);
    font-weight: bold; } }

#app {
  width: 100%;
  height: 100%;
  background: get-theme(light, secondary);
  display: flex;
  flex-direction: row;
  &.dark {
    background: get-theme(dark, secondary); } }

#side-menu {
  width: 10%;
  height: 100%;
  overflow: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 0 32px 0 #0003;
  z-index: 9; }

#main {
  width: 90%;
  height: 100%;
  display: flex;
  flex-direction: row; }

#messaging {
  position: relative;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  #messages {
    flex: 1;
    overflow: auto; }
  .bubble-container {
    display: flex;
    flex-direction: column;
    margin-top: 1px; }
  .bubble {
    max-width: 50%;
    padding: 8px calc(1% + 16px);
    color: #000a;
    @at-root {
      @include gradient(&); }
    @at-root #app.dark & {
      color: #fffc; } }
  #msg-input {
    height: 64px;
    padding: 8px;
    box-sizing: border-box;
    box-shadow: 0 0 16px 0 #0003;
    display: flex;
    flex-direction: row;
    align-items: center;
    input {
      flex: 1;
      margin-right: 16px;
      padding: 0 5%;
      height: 100%;
      border: none;
      border-radius: 999px;
      outline: none;
      background: none;
      box-shadow: inset 0 0 16px 0 #0003;
      @include get-style(color);
      font-size: 1em; }
    h1 {
      height: 100%;
      display: flex;
      align-items: center;
      @include get-style(color);
      cursor: pointer; } } }

#visiting {
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
  .avatar {
    border-style: solid;
    border-width: 1vw;
    @include get-style(border-color, 2);
    box-shadow: 0 0 16px 0 #0003, inset 0 0 16px 0 #0003; }
  .name {
    margin-top: 5%;
    @include get-style(color); }
  #share-container {
    margin-top: 5%;
    width: 80%; }
  .shares {
    @include get-style(color);
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    h3 {
      flex: 100%;
      position: relative;
      padding: 16px 0 16px 5%;
      box-sizing: border-box;
      transition: background .5s;
      cursor: pointer;
      display: flex;
      align-items: center;
      &:hover {
        background: #0003; }
      &:before, &:after {
        content: '';
        position: absolute;
        right: 5%;
        width: 8px;
        height: 2px;
        transform: rotate(45deg);
        transition: transform .5s; }
      &:after {
        transform: translate(-5px) rotate(-45deg); }
      @at-root {
        @include gradient(&, ':before');
        @include gradient(&, ':after'); }
      &:hover:before {
        transform: translate(-5px) rotate(45deg); }
      &:hover:after {
        transform: rotate(-45deg); } } }
  .fig-container {
    flex: 0 50%;
    position: relative;
    padding-top: 50%;
    background: linear-gradient(45deg, #3338 25%, transparent 25%), linear-gradient(-45deg, #3338 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #3338 75%), linear-gradient(-45deg, transparent 75%, #3338 75%);
    background-size: 20px 20px;
    background-position: 0 0, 0 10px, 10px -10px, -10px 0px; }
  figure {
    margin: 0;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%; } }
